.page-container {
	position: relative; }

.page-content {
	width: 100%;
	@include md {
		max-width: 700px; }
	@include lg {
		max-width: 800px; }
	@include xlg {
		max-width: 1140px; } }

.page-map {
	position: fixed;
	@include d-size(top, 130);
	right: 0;
	height: auto;
	max-width: 100%;
	width: 100%;
	z-index: 10;
	background: $green_extra_light;
	@include cover;
	@include md {
		max-width: calc(100% - 700px);
		height: 100vh; }
	@include lg {
		max-width: calc(100% - 800px); }
	@include xlg {
		max-width: calc(100% - 1140px); }

	&.is-fixed {
		@include d-size(top, 90); } }
