.filters {
	padding: 15px 20px 20px;
	@include sm {
		padding: 25px 0 40px; }
	@include lg {
		padding: 40px 0; }
	@include d-size(padding-left, 35);
	@include d-size(padding-right, 35);

	&__field {
		&:not(:last-child) {
			margin-bottom: 20px; } }

	&__actions {
		display: flex; }

	&__bottom {
		display: flex;
		justify-content: space-between; } }

.filter-types {
	display: flex;
	justify-content: space-between;
	&__list {
		display: flex; }

	&__item:first-child {
		span {
			margin-left: 0; } } }

.filter-action {
	display: flex;
	align-items: center;
	flex-grow: 1;
	flex-shrink: 1;
	@include d-size(flex-basis, 273);
	@include d-size(height, 72);
	border-radius: 4px;
	padding: 0 15px;
	background-color: $green_light;
	cursor: pointer;
	@include sm {
		border-radius: 8px; }

	&_type {
		@include d-size(flex-basis, 504); }

	&:not(:last-child) {
		margin-right: 5px;
		@include sm {
			margin-right: 10px; } }

	&__label {
		font-size: 10px;
		@include d-size(font-size, 20);
		@include bold; }

	&__img {
		width: 20px;
		margin-right: 10px;
		@include d-size(margin-right, 35);
		font-size: 0;
		svg {
			width: 100%; }

		.filter-action_district & {
			@include d-size(width, 37);
			// svg
 }			// 	+d-size(height, 36)

		.filter-action_more & {
			@include d-size(width, 30);
			// svg
 } } }			// 	+d-size(height, 8)

.filters-show-btn {
	flex-grow: 0;
	flex-shrink: 1;
	@include d-size(flex-basis, 273); }

.filters-results {
	@include d-size(font-size, 24);
	color: $green; }

.reset-filters {
	position: relative;
	@include d-size(padding-left, 30);
	cursor: pointer;

	&:before,
	&:after {
		content: '';
		position: absolute;
		@include d-size(top, 5);
		@include d-size(left, 10);
		width: 1px;
		@include d-size(height, 22);
		border-radius: 1px;
		background-color: $green; }
	&:before {
		transform: rotate(45deg); }
	&:after {
		transform: rotate(-45deg); }

	span {
		@include d-size(font-size, 24);
		color: $green;
		border-bottom: 1px solid $green;
		@include trans; }

	&:hover {
		span {
			border-bottom-color: transparent; } } }
