.header {
	position: relative;
	z-index: 12;
	box-shadow: 0 2px 3px rgba(#000, 0.2);

	@include sm {
		box-shadow: 0 7px 10px rgba(#000, 0.2); }
	&.is-fixed {
		position: fixed;
		top: 0;
		left: 0;
		right: 0;
		background-color: $white;
		animation: fromTop 1s; }
	&__inner {
		display: flex;
		flex-flow: row nowrap;
		justify-content: space-between;
		align-items: center;
		height: 52px;
		@include d-size(height, 130);
		.is-fixed & {
			@include d-size(height, 90); } }

	&__logo {
		margin-top: -5px;
		.is-fixed & {
			margin-top: 0; } }

	&__logo-link {
		display: flex;
		justify-content: space-between;
		align-items: center;
		text-decoration: none;
		img {
			width: 100%; } }

	&__logo-img {
		max-width: 20px;
		@include d-size(max-width, 50);
		margin-right: 5px;
		@include trans;
		.is-fixed & {
			@include d-size(max-width, 33); }
		@include sm {
			margin-right: 10px; } }

	&__logo-text {
		max-width: 80px;
		@include d-size(max-width, 215);
		@include trans;
		.is-fixed & {
			opacity: 0;
			visibility: hidden; } }

	&__dropdown {
		margin-left: auto;
		margin-right: 30px;
		@include xlg {
			@include d-size(margin-right, 50); } }

	&__nav {
		@include md {
			@include d-size(margin-right, 70); }
		@include lg {
			@include d-size(margin-right, 50); } }
	&__login {
		display: flex;
		align-items: center;
		@include d-size(margin-left, 70); }

	&__menu-wrap {
		position: absolute;
		top: 0;
		right: 0;
		height: 100vh;
		min-height: 100%;
		display: flex;
		flex-flow: column;
		justify-content: space-between;
		transform: translateX(105%);
		transition: transform .15s ease-in-out;
		padding: 60px 20px 15px;
		box-shadow: 0px 5px 5px -3px rgba(0, 0, 0, 0.2), 0px 8px 10px 1px rgba(0, 0, 0, 0.14), 0px 3px 14px 2px rgba(0, 0, 0, 0.12);
		background-color: $white;
		@include md {
			position: relative;
			top: auto;
			right: auto;
			height: auto;
			min-height: auto;
			align-items: center;
			justify-content: space-between;
			flex-flow: row;
			padding: 0;
			box-shadow: none;
			// flex-grow: 1
			transform: none; }


		.is-open & {
			transform: translateX(0); } }

	&__mob-btn {
		@include md {
			display: none; } } }

.dropdown {
	position: relative;
	&.is-open {
		.dropdown__list {
			visibility: visible;
			opacity: 1; } }

	&__title {
		display: flex;
		justify-content: space-between;
		cursor: pointer; }

	&__title-text {
		margin-right: 5px;
		@include bold;
		@include d-size(font-size, 30);
		@include sm {
			margin-right: 10px; } }

	&__list {
		position: absolute;
		top: 100%;
		margin-top: 10px;
		min-width: 200px;
		// max-width: 250px
		// max-height: 200px
		padding: 10px 0 5px;
		overflow-y: auto;
		list-style: none;
		visibility: hidden;
		opacity: 0;
		@include trans;
		background-color: $white;
		box-shadow: 0px 5px 5px -3px rgba(0, 0, 0, 0.2), 0px 8px 10px 1px rgba(0, 0, 0, 0.14), 0px 3px 14px 2px rgba(0, 0, 0, 0.12);
		z-index: 10;
		border-radius: 4px; }
	&__list-link {
		display: block;
		padding: 7px 15px;
		@include d-size(font-size, 24);
		text-decoration: none;
		color: $text;
		@include trans;
		&:hover {
			background-color: $green_light; } }

	.icon-chevron {
		margin-top: 4px;
		width: 12px;
		@include d-size(width, 27);
		@include rmin(1200) {
			margin-top: 8px; }
		@include rmin(1600) {
			margin-top: 14px; }
		.is-open & {
			transform: rotate(180deg); } } }
.nav {
	flex: 1;
	&__list {
		display: flex;
		flex-flow: column;
		align-items: space-between;
		text-align: right;
		@include md {
			flex-flow: row;
			align-items: center; } }

	&__item {
		@include md {
			// +d-size(padding-left, 25)
			// +d-size(padding-right, 25)
			@include d-size(padding-left, 10);
			@include d-size(padding-right, 10); } }

	&__link {
		display: inline-block;
		font-size: 19px;
		padding: 7px 0;

		border-bottom: 1px solid transparent;
		color: $text;
		text-decoration: none;
		@include trans;
		@include md {
			display: inline-block;
			padding: 0;
			@include d-size(font-size, 24); }

		&:hover {
			color: $text_green_bright;
			border-color: $text_green_bright; } } }

@keyframes fromTop {
	from {
		transform: translateY(-100%); }
	to {
		transform: translateY(0); } }
