.pagination {
	display: flex;
	justify-content: space-between;
	align-items: center;
	@include d-size(padding-top, 32);
	@include d-size(padding-bottom, 50);
	@include d-size(padding-left, 50);
	@include d-size(padding-right, 50);

	&__list {
		display: flex; }

	&__item {
		&:not(:last-child) {
			margin-right: 10px; } }

	&__link {
		display: inline-block;
		@include d-size(font-size, 24);
		min-width: 20px;
		color: $text;
		border-bottom: 1px solid transparent;
		text-decoration: none;
		text-align: center;
		@include trans;
		&:hover {
			border-color: currentColor;
			color: $green; }

		&.is-active {
			border-color: currentColor;
			color: $green; } }

	&__link_dots {
		&:before {
			content: '...'; } } }

